export interface AbandonedCart {
  /**
   * Original cart ID
   * @format GUID
   */
  _id?: string;
  /** Cart status */
  status?: StatusWithLiterals;
  /**
   * Time the cart was abandoned
   * @readonly
   */
  abandonTime?: Date | null;
  /** Buyer information */
  buyerInfo?: BuyerInfo;
  /** Cart total including currency symbol */
  total?: string | null;
  /**
   * History activities
   * @readonly
   */
  activities?: Activity[];
}

export enum Status {
  ABANDONED = 'ABANDONED',
  RECOVERED = 'RECOVERED',
}

/** @enumType */
export type StatusWithLiterals = Status | 'ABANDONED' | 'RECOVERED';

export interface BuyerInfo {
  /**
   * Wix customer ID
   * @format GUID
   */
  _id?: string;
  /** Customer information */
  identityType?: IdentityWithLiterals;
  /**
   * Customer's email address
   * @maxLength 50
   * @format EMAIL
   */
  email?: string | null;
  /**
   * Customer's phone number
   * @format PHONE
   */
  phone?: string | null;
  /** Customer's first name */
  firstName?: string | null;
  /** Customer's last name */
  lastName?: string | null;
}

export enum Identity {
  /** Customer is the site owner */
  ADMIN = 'ADMIN',
  /** Customer is logged in */
  MEMBER = 'MEMBER',
  /** Customer is not logged in */
  VISITOR = 'VISITOR',
  /** Contact was created for the customer */
  CONTACT = 'CONTACT',
}

/** @enumType */
export type IdentityWithLiterals =
  | Identity
  | 'ADMIN'
  | 'MEMBER'
  | 'VISITOR'
  | 'CONTACT';

export interface Activity {
  /**
   * Log item type
   * @readonly
   */
  activityType?: ActivityTypeWithLiterals;
  /**
   * Comment added to Log item
   * @maxLength 1000
   * @readonly
   */
  message?: string | null;
  /**
   * Log item occurrence timestamp
   * @readonly
   */
  timestamp?: Date | null;
  /** Custom data for un-typed activities */
  customData?: CustomData;
}

export enum ActivityType {
  UNRECOGNIZED_TYPE = 'UNRECOGNIZED_TYPE',
  SCHEDULED = 'SCHEDULED',
  EMAIL_SENT = 'EMAIL_SENT',
  EMAIL_NOT_SENT = 'EMAIL_NOT_SENT',
  NOTIFICATION_SENT = 'NOTIFICATION_SENT',
  TASK_CREATED = 'TASK_CREATED',
  CUSTOM_ACTIVITY = 'CUSTOM_ACTIVITY',
}

/** @enumType */
export type ActivityTypeWithLiterals =
  | ActivityType
  | 'UNRECOGNIZED_TYPE'
  | 'SCHEDULED'
  | 'EMAIL_SENT'
  | 'EMAIL_NOT_SENT'
  | 'NOTIFICATION_SENT'
  | 'TASK_CREATED'
  | 'CUSTOM_ACTIVITY';

export interface CustomData {
  /**
   * Activity unique namespace
   * @maxLength 50
   */
  namespace?: string | null;
  /** Custom json field for any desired data */
  customValue?: Record<string, any> | null;
}

export interface CartAbandonedEvent {
  /** @format GUID */
  cartId?: string;
  /**
   * Time the cart was created
   * @readonly
   */
  creationTime?: Date | null;
  /**
   * Time the cart was abandoned
   * @readonly
   */
  abandonTime?: Date | null;
  /** Buyer information */
  buyerInfo?: BuyerInfo;
  /** Amount of items in cart */
  itemsCount?: number;
  /** Coupon ID (if relevant) */
  couponId?: string;
  /** Subtotal of all line items in cart, not before shipping and taxes */
  totals?: Totals;
  /**
   * Checkout URL - checkout with the abandoned cart details
   * @format WEB_URL
   */
  checkoutUrl?: string;
}

export interface Totals {
  /**
   * Subtotal of all line items in cart, without shipping and taxes
   * @max 999999999.99
   */
  subtotal?: number | null;
  /**
   * Total cart price
   * @max 999999999.99
   */
  total?: number | null;
  /** Formatted total cart price includes currency symbol */
  formattedTotal?: string;
}

export interface CartRecoveredEvent {
  /** @format GUID */
  cartId?: string;
  /**
   * Time the cart was recovered
   * @readonly
   */
  recoveredTime?: Date | null;
  /**
   * Time the cart was created
   * @readonly
   */
  creationTime?: Date | null;
  /**
   * Time the cart was abandoned
   * @readonly
   */
  abandonedTime?: Date | null;
}

export interface CartDeletedEvent {
  /** @format GUID */
  instanceId?: string;
  /** @format GUID */
  cartId?: string;
}

